import React from 'react';
import './App.css';
import Header from './components/header';
import Body from './components/body';

class App extends React.Component {
  constructor(){
    super();
    this.state = {
      language:"EN"
    };
  }
  render(){
    return (
      <div className="App">
        <Header language={this.state.language}/>
        <Body language={this.state.language}/>
      </div>
    );
  }
}

export default App;
