import React from 'react';

class Header extends React.Component {
    render() {
        return (
            <div id="header">
                <header>
                <ul className="left">
                    <li><a href={process.env.PUBLIC_URL}><img alt="brand-logo" height="30px" width="auto" className="header-logo" src={process.env.PUBLIC_URL+"/images/logo.png"}/></a></li>
                </ul>
                <ul className="right">
                </ul>
                </header>
            </div>
        )
    }
 }
 
 export default Header;